<template>
    <div>
        <h2 class="title">
            {{$route.name == '修改定时菜单' ? '修改定时菜单栏配置' : '配置新定时菜单栏'}}
        </h2>
        <div class="edit-info">
            <Form :model="formData" class="form-group" label-position="right" :label-width="100">
                <FormItem label="选择公号" prop="codes">
                    <CheckboxGroup v-model="codes">
                        <Checkbox v-for="(item, index) in gonghaoList" :label="item.code" name="codes">{{item.name}}</Checkbox>
                    </CheckboxGroup>
                </FormItem>
                <FormItem label="标题" prop="title">
                    <Input size="large" type="text" v-model="title" placeholder="请输入标题"></Input>
                </FormItem>
                <FormItem label="定时时间" prop="time">
                    <DatePicker placement="top-start" type="datetime" placeholder="选择日期时间" style="width: 200px" v-model="time"></DatePicker>
                </FormItem>
                <FormItem label="二级菜单" prop="has_sub_btn">
                    <i-switch v-model="has_sub_btn" />
                </FormItem>
                <FormItem label="一级菜单名称" prop="name">
                    <Input size="large" type="text" v-model="formData.name" placeholder="请输入一级菜单名称"></Input>
                </FormItem>
                <!-- 没有二级菜单 -->
                <FormItem label="链接/关键词"  v-show="!has_sub_btn">
                    <RadioGroup v-model="formData.type">
                        <Radio label="view">view</Radio>
                        <Radio label="click">click</Radio>
                    </RadioGroup>
                </FormItem>
                <FormItem label="一级菜单链接" prop="url" v-show="!has_sub_btn && formData.type == 'view'">
                    <Input size="large" type="text" v-model="formData.url" placeholder="请输入一级菜单链接"></Input>
                </FormItem>
                <FormItem label="一级菜单关键字" prop="key" v-show="!has_sub_btn && formData.type == 'click'">
                    <Input size="large" type="text" v-model="formData.key" placeholder="请输入一级菜单关键字"></Input>
                </FormItem>
                <!-- 没有二级菜单   end    -->
                <!-- 有二级菜单 -->
                <FormItem label="二级菜单名称" prop="name" v-show="has_sub_btn">
                    <Input size="large" type="text" v-model="subItem.name" placeholder="请输入二级菜单名称"></Input>
                </FormItem>
                <FormItem label="链接/关键词" v-show="has_sub_btn">
                    <RadioGroup v-model="subItem.type">
                        <Radio label="view">view</Radio>
                        <Radio label="click">click</Radio>
                    </RadioGroup>
                </FormItem>
                <FormItem label="二级菜单链接" prop="url" v-show="has_sub_btn && subItem.type == 'view'">
                    <Input size="large" type="text" v-model="subItem.url" placeholder="请输入二级菜单链接"></Input>
                </FormItem>
                <FormItem label="二级菜单关键词" prop="key" v-show="has_sub_btn && subItem.type == 'click'">
                    <Input size="large" type="text" v-model="subItem.key" placeholder="请输入二级菜单关键词"></Input>
                </FormItem>
                <Button size="large" type="primary" v-show="has_sub_btn && !isUpdate1" @click="addSubBtn()">添加二级菜单</Button>
                <Button size="large" type="success" v-show="isUpdate1" @click="saveSecond()">保存</Button>
                <span class="line" v-show="has_sub_btn"></span>
                <Button size="large" type="primary" @click="addBtn()">添加一级菜单</Button>
                <Button size="large" type="success" @click="saveFirst()" v-show="isUpdate" style="margin-left: 30px;">保存</Button>
                <Table class="first-table" stripe :columns="firstHeader" :data="firstData"></Table>
                <Button class="btn" size="large" type="primary" @click="submitForm">提交</Button>
                <Button size="large" type="error" @click="cancel">取消</Button>
            </Form>
        </div>
        <div class="sub-btn-table">
            <Table class="second-table" stripe :columns="secHeader" :data="secData"></Table>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                codes: [],
                formData: {
                    sub_button: [],
                    type: "view"
                },
                title: '',
                time: new Date(),
                firstData: [],
                has_sub_btn: false,
                subItem: {
                    type: "view"
                },
                radio: "url",
                isUpdate: false,
                isUpdate1: false,
                secData: [],
                firstHeader: [
                    {
                        key: 'name',
                        title: '一级菜单名称',
                        align: 'center',
                        render: (h, params) => {
                            let name = (params.row.name ? params.row.name : '~')
                            return h('span', {
                                props: {}
                            }, name)
                        }
                    }, {
                        title: 'Action',
                        align: 'center',
                        render: (h, params) => {
                            let that = this;
                            return h('div', [
                                h('Button', {
                                    style: {
                                        marginRight: '10px'
                                    },
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    on: {
                                        click() {
                                            that.formData = params.row;
                                            if(that.formData.sub_button != []) {
                                                that.secData = that.formData.sub_button 
                                                that.has_sub_btn = true
                                                that.subItem = that.secData[0]
                                            }
                                        }
                                    }
                                }, '编辑'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click() {
                                            that.firstData.splice(index, 1);
                                            that.formData = {
                                                sub_button: [],
                                                type: "view"
                                            };
                                        }
                                    }
                                }, '删除')
                            ])
                        }
                    }
                ],
                secHeader: [
                    {
                        key: 'name',
                        title: '二级菜单名称',
                        align: 'center'                  
                    }, {
                        title: 'Action',
                        align: 'center',
                        render: (h, params) => {
                            let that = this;
                            return h('div', [
                                h('Button', {
                                    style: {
                                        marginRight: '10px'
                                    },
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    on: {
                                        click() {
                                            that.subItem = params.row;
                                            that.isUpdate1 = true;
                                        }
                                    }
                                }, '编辑'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click() {
                                            that.formData.sub_button.splice(index, 1);
                                            that.subItem = {
                                                type: "view"
                                            };
                                            that.has_sub_btn = true
                                        }
                                    }
                                }, '删除')
                            ])
                        }
                    }
                ],
                gonghaoList: []
            };
        },
        mounted() {
            this.showGonghaoList()
            if(this.$route.name === '修改定时菜单') {
                this.isUpdate = true;
                this.data_info = this.$route.params
                this.codes = this.data_info.codes;
                this.title = this.data_info.title;
                this.time = this.data_info.time;
                this.firstData = this.data_info.values;
                this.formData = this.firstData[0];
            }
        },
        methods: {
            showGonghaoList() {
                this.$axios.get("/conf")
                .then(res => {
                    this.gonghaoList = res.data.data;
                });
            },
            cancel() {
                this.resetForm()
                this.$router.push({name: '配置定时菜单'})
            },
            resetForm() {
                this.formData = {
                    sub_button: [],
                    type: "view"
                };
                this.subItem = {
                    type: "view"
                }
            },
            addBtn() {
                this.formData.sub_button = this.secData
                this.secData = [];
                if(this.formData.sub_button.length == 0) {
                    delete this.formData.sub_button
                }
                this.firstData.push(this.formData);
                console.log(this.firstData)
                this.resetForm()
                this.has_sub_btn = false
            },
            submitForm() {
                if (this.$route.name === '新建定时菜单') {
                    this.$axios.post('/menuTime/create',{
                        codes: this.codes,
                        title: this.title,
                        time: this.time,
                        values: this.firstData
                    }).then(res => {
                        this.firstData = [];
                        this.$Message.info('创建成功')
                        this.cancel()
                    });
                } else if(this.$route.name === '修改定时菜单') {
                    this.$axios.post('/menuTime/update', {
                        id: this.data_info._id,
                        codes: this.codes,
                        title: this.title,
                        time: this.time,
                        values: this.firstData
                    }).then(res => {
                        this.firstData = [];
                        this.$Message.info('修改成功')
                        this.cancel()
                    });
                }
            },
            addSubBtn() {
                this.secData.push(this.subItem);
                this.subItem = {
                    type: "view"
                }
                this.has_sub_btn = true
            },
            saveFirst() {
                this.resetForm()
                this.has_sub_btn = false
            },
            saveSecond() {
                this.resetForm()
                this.isUpdate1 = false;
                this.has_sub_btn = false
            }
        }
    };
</script>

<style scoped>
    .form-group {
      width: 500px;
    }
    .edit-info {
      float: left;
      margin-bottom: 30px;
    }
    .title {
        font-size: 30px;
      margin-bottom: 30px;
    }
    .sub-btn-table {
      float: left;
      width: 500px;
      margin: 150px 0 0 50px;
    }
    .line {
      width: 480px;
      height: 30px;
      margin: 30px 40px 0;
      display: block;
      border-top: 1px dashed #000;
    }
    .first-table {
        margin: 30px 0;
    }
    .btn {
        margin-right: 20px;
    }
</style>
